.pageHome {
  display: table;
  width: 100%;
  max-width: 100%;
  text-align: center;
  position: relative;

  &__block {
    display: table-cell;
    vertical-align: middle;

    &::after {
      content: ' ';
      clear: both;
    }
  }

  &__name {
    font-size: 70px;
    font-weight: 700;
    color: var(--bs-primary);
  }

  &__jobTitle {
    position: absolute;
    overflow: hidden;
    max-width: 100%;
    width: calc(100% - 30px);
  }

  &__jobTitleDetail {
    font-size: 21px;
    font-weight: 300;
    color: #222;
  }
}
